
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import ActionSheet from '@/components/ActionSheet/actionsheet'
import commonFn from '@/assets/js/DictionaryTableFn.js';
export default {
components: {
XHeader,Group,Calendar,PopupPicker,Datetime,ActionSheet,XInput
},
data(){
return{
details: 1,	// 查看状态，1表示新增，2表示编辑详情，3表示查看详情，
detailsInfo: '',	// 详细信息
username:'',
usercode:'',
genter: [],
idcard:'',
relation:[],
iphone:'',
zjType:[['居民身份证','居民户口簿','护照','军官证(士兵证)','驾驶执照','港澳居民来往内地通行证','台湾居民来往内地通行证','其他']],
zj:[],
list:[['男','女','未知性别','未说明性别']],
gxList:[['朋友','家人','自己']],
Time: '',
CBM: '',
JRlist: [], //选择家人展示的列表
selectJR: false, // 选择家人是否展示标识符
}

},
mounted(){
if(typeof this.$route.params != 'undefined' && this.$route.params.details){  // 查看状态，1表示新增，2表示编辑详情，3表示查看详情，
this.details = 3;
this.detailsInfo = this.$route.params.details;
}else{
if(this.$route.params.edit){
this.details = 2;
let info = this.$route.params.edit;
this.username = info.CHZXM;
this.usercode = info.CHZBM;
this.zj = [info.CZJLXMC];
this.idcard = info.CZJHM;
this.genter = [info.CXBMC];
this.relation[0] = info.CGXMS;
this.Time = info.DCSNY;
this.iphone = info.CSJH;
this.CBM = info.CBM;
} else {
this.details = 1;
if (this.$root.loginInfo.jkdabh.length > 0) {
this.qryMyFamilyList();
}
}
}
},
watch: {
idcard(val) {
if (val.length >= 18) {
if (this.isCardNo(val) == false) {
this.$vux.toast.show({
type:'cancel',
time: '1000',
text: '身份证格式不合法!',
onHide(){
}
})
}
}
}
},
methods: {
isCardNo(card) {
var reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/;
if(reg.test(card) === false) {
return false;
}
return true;
},
changeTime(val){
this.Time = val;
},
qryMyFamilyList() { // 请求我的家人
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "wdjt",
"MSH.2": "qryMyFamilyList",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(res && res.length > 0) {
res.forEach(item=> {
this.JRlist.push({
name: item.JMXM,
key: item,
});
});
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
selectJRFunction(val) { // 选择家人
this.username = val.name;
this.usercode = '';
},
save(){
if (this.idcard.length >= 18) {
if (this.isCardNo(this.idcard) == false) {
this.$vux.toast.show({
type:'cancel',
time: '1000',
text: '身份证格式不合法!',
onHide(){
}
})
return;
}
}
this.$ajax.post('',{
"MSH.1": "visitor",
"MSH.2": "addOrUpdateVisiter",
token:this.$root.loginInfo.token, //token
idcardTypeBm: commonFn.getIdcardTypeBM("居民身份证"),	// 证件类型编码
idcardno:this.idcard,	// 证件号码
jzrxm:this.username,	// 就诊人姓名
jzrbm: this.usercode,	// 就诊人编码
zexbm:commonFn.getGenterBM(this.genter[0]),	// 性别编码
ralation:this.relation[0],	// 关系
birthday:this.Time,	// 生日
tel:this.iphone,	// 手机号码
cbm: this.CBM,  // 纠正人记录编码  (修改时候才填)
}).then(res => {
let THIS = this;
if(typeof res.status != 'undefined' && res.status == 1){
this.$vux.toast.show({
text: this.details == 1 ? '添加成功!' : '编辑成功',
type: 'success',
onHide: function(){
THIS.$router.goBack();
}
});

}


}).catch(error => {
console.log(error);
})
}
}
}



